.banner-container {
  display: flex;
  width: 70%;

  .wish-banner {
    color: white;
    font-size: 80px;
    background: #67009180;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding: 0px 20px 0px 100px;
    letter-spacing: 20px;

    .sub-section {
      font-size: 26px;
      margin: 20px 0px 0px 74px;
      letter-spacing: 6px;
    }
  }
}
.form-container {
  text-align: center;
  width: 100%;
  background-color: #0000008a;
  color: #fff;
  padding: 30px 80px 50px 60px;
  min-width: 200px;
}
.side-panel {
  display: flex;
  width: 30%;
}
.login-register-link {
  padding-top: 50px;
}
.login-register-link a {
  color: #fff;
  text-decoration: none;
}

.products-container {
  display: flex;
  margin-top: 64px;
  justify-content: center;

  .filter-wrapper {
    width: 15%;
    padding: 10px 5px;
    border-right: 1px solid #e0e0e0;
    min-width: 215px;

    .filter-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 8px;
      position: fixed;
      width: inherit;
      overflow-y: auto;
      overflow-x: hidden;
      height: calc(100% - 90px);
      min-width: 215px;

      .MuiListItem-dense {
        padding-top: 0px;
        padding-bottom: 0px;
      }
    }
  }

  .results-wrapper {
    width: 65%;
    padding: 10px 5px;

    .search-bar {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .product-subtitle {
        font-size: 0.8rem;
        padding-left: 10px;
      }
    }

    .results-container {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
      grid-auto-rows: auto;
      grid-gap: 10px 5px;
      padding: 20px 0px;
  
      .quantity-button {
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        width: 100%;
      }
    }
  }

  .cart-container {
    width: 20%;
    padding: 10px 5px;
    margin-right: 10px;
    border-left: 1px solid #e0e0e0;
  }

  /* Scrollbar width */
  ::-webkit-scrollbar {
    width: 6px;
  }
  /* Track */
  ::-webkit-scrollbar-track {
    background: #ececec;
  }
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #cfcfcf;
    border-radius: 10px;
  }
}

@media screen and (max-width: 1023px) {
  
  .products-container .filter-wrapper {
    width: 18%;
    min-width: 195px;
    .filter-container {
      min-width: 195px;

      .MuiListItem-gutters {
        padding-left: 0px;
      }
    }
  }
  .products-container .results-wrapper {
    width: 80%;
  }
  .cart-container {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  
  .products-container .filter-wrapper {
    display: none;
  }
  .products-container .results-wrapper {
    width: 95%;
  }
}
